import Header from "../../components/Header.vue";
import Footer from "../../components/Footer.vue";
import Condividi from "../../components/Condividi.vue";
import DataHeader from "../../components/DataHeader.vue";
import { StringRenderer } from "../../../commons/js/string_renderer.js";
import "../../js/consiglioinforma.js";
import { Paginazione } from "../../../commons/js/render_paginazione.js";

export default {
    name: "app",
    components: {
        Footer,
        Header,
        Condividi,
        DataHeader
    },
    data: function () {
        $(document).ready(renderNotizie);
        return {};
    }
};


function renderNotizie() {

    var urlParams = new URLSearchParams(window.location.search);
    const idArgomento = urlParams.get('argomento');
    const idOrganismo = urlParams.get('organismo');
    const idCommissione = urlParams.get('commissione');
    const idPresidente = urlParams.get('presidente');
    const idGruppo = urlParams.get('gruppo');
    const tipo = urlParams.get('tipo');

    var size = 10;
    var url_to_call = apibase.baseurl + '10500?size=' + size;
    url_to_call += Paginazione.inizializzaUrl(urlParams);
    const filtro = {
        "SORT": [{
            "direction": "DESC",
            "property": "data_pubblicazione"
        }, {
            "direction": "DESC",
            "property": "data_pubblicazione_time"
        }]
    };
    let ricerca = false;
    if (tipo && tipo == 'approfondimento') {
        filtro.approfondimento = 'T';
        ricerca = true;
    }
    if (idOrganismo) {
        filtro.organismo = idOrganismo;
        ricerca = true;
    }
    if (idCommissione) {
        filtro.commissione = idCommissione;
        ricerca = true;
    }
    if (idPresidente) {
        filtro.presidente = idPresidente;
        ricerca = true;
    }
    if (idGruppo) {
        filtro.gruppo = idGruppo;
        ricerca = true;
    }
    if (idArgomento) {
        filtro.argomento = idArgomento;
        renderBreadcrumbConArgomento(ricerca);
    } else {
        renderBreadcrumb(ricerca);
    }

    $.ajax({
        url: url_to_call,
        method: 'POST',
        data: JSON.stringify(filtro),
        contentType: 'application/json',
        dataType: 'json',
        success: render,
    });

    function renderBreadcrumbConArgomento(ricerca) {
        var urlArgomenti = apibase.baseurl + '10503';
        return $.ajax({
            url: urlArgomenti,
            success: function (result) {
                if (result && result.content) {
                    for (const argomento of result.content) {
                        if (argomento.id == idArgomento) {
                            let bcSezione = '<li class="breadcrumb-item">\
                                                <a href="notizie.html">News</a>\
                                                <span class="separator">/</span>\
                                            </li>\
                                            <li class="breadcrumb-item">' + argomento.title + '</li>';
                            if (ricerca) {
                                bcSezione = '<li class="breadcrumb-item">\
                                                <a href="notizie.html">News</a>\
                                                <span class="separator">/</span>\
                                            </li>\
                                            <li class="breadcrumb-item">\
                                                <a href="notizie.html?argomento=' + idArgomento + '">' + argomento.title + '</a>\
                                                <span class="separator">/</span>\
                                            </li>\
                                            <li class="breadcrumb-item">\
                                                Risultati ricerca\
                                            </li>';
                            }
                            $('#bread').append(bcSezione);
                        }
                    }
                }
            }
        });
    }

    function renderBreadcrumb(ricerca) {
        let bcSezione = '<li class="breadcrumb-item">News</li>';
        if (ricerca) {
            bcSezione = '<li class="breadcrumb-item">\
                            <a href="notizie.html">News</a>\
                            <span class="separator">/</span>\
                        </li>\
                        <li class="breadcrumb-item">\
                            Risultati ricerca\
                        </li>';
        }
        $('#bread').append(bcSezione);
    }

    function render(result) {
        if (result != null && result.content != undefined) {
            var newsHTML = "<div class=\"row ml-3\">\n";
            let elencoNews = result.content;
            elencoNews.forEach(function (single) {
                var paginaDettaglio = 'notizia.html?id=' + single.id;
                var footerElemento = "";
                var stringedDate = "";
                if (single.properties.data_pubblicazione != undefined) {
                    stringedDate = StringRenderer.renderDataTestuale(single.properties.data_pubblicazione);
                } else if (single.lastModifiedDate) {
                    stringedDate = StringRenderer.renderDataTestuale(single.lastModifiedDate);
                }
                if (urlParams.get('ref_type_id') == 10061) {
                    stringedDate = "";
                }

                var descrizione = "";
                var sottotitolo = "";

                if (single.properties.descrizione != undefined && urlParams.get('ref_type_id') != 10030) {
                    descrizione = single.properties.descrizione;
                }
                if (single.properties.sottotitolo != undefined) {
                    sottotitolo = single.properties.sottotitolo;
                }
                if (single.properties.intestazione != undefined) {
                    descrizione = single.properties.intestazione;
                }
                if (descrizione != "") {
                    newsHTML += ' <div class="col-12 dark-obj-name"> ' + stringedDate + '</div> <div class="col-12">  <h5 class="dark-obj-name-bold"><a class="dark-obj-name" href="' + paginaDettaglio + '">' + single.title + " " + sottotitolo + ' <svg class="icon icon-arrow-right"><use xlink:href="svg/sprite.svg#it-chevron-right"></use></svg> </a> </h5></div> <div class="col-12"> <p> ' + descrizione + '  </p> ' + footerElemento + ' <hr class="separator bordered"/> </div> ';
                } else {
                    newsHTML += ' <div class="col-12 dark-obj-name"> ' + stringedDate + '</div> <div class="col-12">  <h5 class="dark-obj-name-bold"><a class="dark-obj-name" href="' + paginaDettaglio + '">' + single.title + " " + sottotitolo + ' <svg class="icon icon-arrow-right"><use xlink:href="svg/sprite.svg#it-chevron-right"></use></svg> </a> </h5> ' + footerElemento + ' <hr class="separator bordered"/> </div>  ';
                }

            });
            $("#fascia_elenco").append(newsHTML);
        }

        $("#fascia_elenco").append(Paginazione.renderizzaComponente(result));

        if (result.totalPages == 0) {
            $("#fascia_elenco").html("Nessun risultato trovato");
        }

    }


}



